.toggle-switch {
    display: flex;
}

    .toggle-switch input[type=checkbox] {
        height: 0;
        width: 0;
        visibility: hidden;
    }

    .toggle-switch label {
        cursor: pointer;
        text-indent: 4em;
        width: 3em;
        height: 1.5em;
        background: #fff;
        border: 1px solid #ddd;
        display: block;
        border-radius: 100px;
        position: relative;
        white-space: nowrap;
    }

        .toggle-switch label:after {
            content: "";
            position: absolute;
            top: 1px;
            left: 0px;
            width: 1.2em;
            height: 1.2em;
            background: #ddd;
            border-radius: 90px;
            transition: 0.3s;
            
        }

    .toggle-switch input:checked + label {
        border:1px solid #f8b113;

    }

        .toggle-switch input:checked + label:after {
            left: calc(100% - 0px);
            transform: translateX(-100%);
            background: #f8b113;;
        }

