.side-panel {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    visibility: hidden;
    transition: visibility 0s 0.6s;
    z-index: 999;
}

.side-panel__header, .side-panel__container, .side-panel__footer {
    position: fixed;
}

.side-panel__footer {
    bottom: -50px;
    right: 0;
    display: flex;
    justify-content: end;
    align-items: center;
    padding: 10px;
    border-top: 1px solid #dddddd;
}

.side-panel.small .side-panel__header,
.side-panel.small .side-panel__container,
.side-panel.small .side-panel__footer {
    width: 30em;
}

@media only screen and (max-width: 48em) {
    .side-panel.small .side-panel__header,
    .side-panel.small .side-panel__container,
    .side-panel.small .side-panel__footer {
        width: 70%;
    }
}

.side-panel.medium .side-panel__header,
.side-panel.medium .side-panel__container,
.side-panel.medium .side-panel__footer {
    width: 50%;
}

@media only screen and (max-width: 62em) {
    .side-panel.medium .side-panel__header,
    .side-panel.medium .side-panel__container,
    .side-panel.medium .side-panel__footer {
        width: 100%;
    }
}

.side-panel.large .side-panel__header,
.side-panel.large .side-panel__container,
.side-panel.large .side-panel__footer {
    width: 70em;
}

.side-panel:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    cursor: pointer;
    transition: background 0.3s 0.3s;
}

.side-panel__header {
    height: 50px;
    line-height: 50px;
    background: #fff;
    z-index: 2;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.03);
    transition: transform 0.3s 0s;
    transform: translateY(-50px);
    border-bottom: 1px solid #dddddd;
}

    .side-panel__header h1 {
        padding-left: 1.4em;
        color: #2E3451;
        font-size: 1.2em;
        font-weight: 600;
    }

.side-panel__footer {
    height: 50px;
    line-height: 50px;
    background: #fff;
    z-index: 2;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.03);
    transition: transform 0.3s 0s;
    transform: translateY(0px);
}

.side-panel__close {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 60px;
    /* image replacement */
    display: inline-block;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    border: 0;
}

    .side-panel__close::before, .side-panel__close::after {
        content: "";
        position: absolute;
        top: 22px;
        left: 20px;
        height: 2px;
        width: 18px;
        background-color: #2E3451;
        backface-visibility: hidden;
    }

    .side-panel__close::before {
        transform: rotate(45deg);
    }

    .side-panel__close::after {
        transform: rotate(-45deg);
    }

    .side-panel__close:hover {
        background-color: #159EEB;
    }

        .side-panel__close:hover::before {
            transform: rotate(220deg);
        }

        .side-panel__close:hover::after {
            transform: rotate(135deg);
        }

        .side-panel__close:hover::before, .side-panel__close:hover::after {
            background-color: #fff;
            transition: transform 0.3s;
        }

.side-panel__container {
    height: 100%;
    top: 0;
    background: #F2F6FA;
    z-index: 1;
    transition: transform 0.3s 0.3s;
}

.side-panel__content {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    height: calc(100% - 100px);
    padding: 1.4em;
    overflow: auto;
    /* smooth scrolling on touch devices */
    -webkit-overflow-scrolling: touch;
}

.side-panel.side-panel--is-visible {
    visibility: visible;
    transition: visibility 0s 0s;
}

    .side-panel.side-panel--is-visible::after {
        background: rgba(0, 0, 0, 0.6);
        transition: background 0.3s 0s;
    }

.side-panel--from-right .side-panel__header {
    right: 0;
}

.side-panel--from-left .side-panel__header {
    left: 0;
}

.side-panel--is-visible .side-panel__header {
    transition: transform 0.3s 0.3s;
    transform: translateY(0px);
}

.side-panel--is-visible .side-panel__footer {
    transition: transform 0.3s 0.3s;
    transform: translateY(-50px);
}

.side-panel--is-visible .side-panel__close::before {
    animation: side-close-1 0.6s 0.3s;
}

.side-panel--is-visible .side-panel__close::after {
    animation: side-close-2 0.6s 0.3s;
}

.side-panel--from-right .side-panel__container {
    right: 0;
    transform: translate3d(100%, 0, 0);
}

.side-panel--from-left .side-panel__container {
    left: 0;
    transform: translate3d(-100%, 0, 0);
}

.side-panel--is-visible .side-panel__container {
    transform: translate3d(0, 0, 0);
    transition-delay: 0s;
}

@keyframes side-close-1 {
    0%, 50% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(45deg);
    }
}

@keyframes side-close-2 {
    0%, 50% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-45deg);
    }
}

