.dropdown {
    perspective: 1000px;
    z-index: 100;
}

    .dropdown:hover .dropdown_menu li {
        display: block;
    }

.dropdown_menu {
    position: absolute;
    top: 100%;
    right: -7px;
    width: 200px;
    perspective: 1000px;
    z-index: 999;
    background-color: #fff;
    padding-top: 1.2em;
    box-shadow: 0 1px 2px rgba(204, 210, 218, 0.07), 0 2px 4px rgba(204, 210, 218, 0.07), 0 4px 8px rgba(204, 210, 218, 0.07), 0 8px 16px rgba(204, 210, 218, 0.07), 0 16px 32px rgba(204, 210, 218, 0.07), 0 32px 64px rgba(204, 210, 218, 0.07);
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}

    .dropdown_menu li {
        display: none;
        color: #626D8A;
        padding: 10px 20px;
        opacity: 0;
        cursor: pointer;
    }

        .dropdown_menu li:hover {
            background-color: #159EEB;
            color: #fff;
        }

.dddropdown .dropdown_menu li:hover {
    background-color: #EBF2FF;
    color: black;
}

.dddropdown .dropdown_menu li {
    display: none;
    color: #626D8A;
    padding: 10px 20px;
    opacity: 0;
    cursor: pointer;
}

.dropdown:hover .dropdown_menu--animated {
    display: block;
}

.dropdown_menu--animated {
    display: none;
}

    .dropdown_menu--animated li {
        display: block;
        opacity: 1;
    }

.dropdown:hover .dropdown_menu--animated-filter {
    display: block;
}

.dropdown_menu--animated-filter li {
    display: block;
    opacity: 1;
}

.down-animate {
    animation: rotateMenu 400ms ease-in-out forwards;
    transform-origin: top center;
}

@keyframes rotateMenu {
    0% {
        transform: rotateX(-90deg);
    }

    70% {
        transform: rotateX(20deg);
    }

    100% {
        transform: rotateX(0deg);
    }
}

